<template>
    <section class='UserOperation'>
        <el-table
                v-if='$route.path==="/admin/adminoperation/userOperation"'
                fit
                highlight-current-row
                :data="showingData"
                style="width: 94%">
            <el-table-column
                    label="用户ID"
                    width="380"
                    align='center'
            >
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.user_id}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="用户名"
                    width="270"
            >
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p><i class='el-icon-user'></i>用户名: {{ scope.row.username }}</p>

<!--                        <p>住址: {{ scope.row.address }}</p>-->
                        <div slot="reference" class="name-wrapper">
<!--                            <i class='el-icon-user'></i>-->
                            <el-tag size="medium">{{ scope.row.username }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column
                    label="密码"
                    width="100"
            >
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p><i class='el-icon-view'></i>密码: {{ decode(scope.row.password) }}</p>
<!--                        <p>住址: {{ scope.row.address }}</p>-->
                        <div slot="reference" class="name-wrapper">
<!--                            <i class='el-icon-view'></i>-->

                            <el-tag size="medium">{{ scope.row.password }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column
                    label="用户操作"
                    align='center'
            >
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
            <el-pagination
                    small
                    @size-chage="handleSizeChange"
                    @current-change="handleCurrentChange"
                    layout="prev, pager, next"
                    :total="count"
                    :page-size="9"
                    style="text-align: center;margin: 15px auto;"
<!--            ></el-pagination>-->
        </el-table>



    <el-table
            v-else
            fit
            highlight-current-row
            :data="showingData"
            style="width: 94%">
        <el-table-column
                label="博文ID"
                width="225"
                align='center'
        >
            <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span style="margin-left: 10px">{{ scope.row.article_id}}</span>
            </template>
        </el-table-column>
        <el-table-column
                label="用户ID"
                width="180"
                align='center'
        >
            <template slot-scope="scope">
                        <el-tag size="medium">{{ scope.row.user_id }}</el-tag>
            </template>
        </el-table-column>
        <el-table-column
                label="发布时间"
                width="270"
                align='center'
        >
            <template slot-scope="scope">
                        <el-tag size="medium">{{ formatDateTime(myTime(scope.row.post_time)) }}</el-tag>
<!--                    </div>-->
<!--                </el-popover>-->
            </template>
        </el-table-column>
        <el-table-column
                label="文章状态"
                width="180"
                align='center'
        >
            <template slot-scope="scope">

                        <el-tag size="medium">{{ scope.row.state===1 ? '已发布博文': '未发布草稿' }}</el-tag>
            </template>
        </el-table-column>
        <el-table-column
                label="文章操作"
                align='center'
        >
            <template slot-scope="scope">
                <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>


    <el-pagination
            small
            @size-chage="handleSizeChange"
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :total="count"
            page-size="9"
    ></el-pagination>




    </section>
</template>

<script>
    const Base64 = require('js-base64').Base64
    export default {
        name: "UserOperation",
        data(){
            return {
                users: [],
                blogs: [],
                count: 0,
                showingData: []
                // drafts: [],
                // articles: []
            }
        },
        created(){
            this.$axios.get('/api/user/queryAllUser')
                .then((response) => {
                    let res = response.data
                    if (res.status) {
                        this.$message.warning(res.msg)
                    } else  {
                        this.users = [...res.result]
                        this.changeData()
                    }
                })

            this.$axios.get('/api/blog/getArticle')
                .then((response) => {
                    let res = response.data
                    if (res.status) {
                        this.$message.warning(res.msg)
                    } else  {
                        this.blogs = [...res.result]
                    }
                })

            // this.exchange()
            // this.blogs = this.$store.state.allBlogs
        },
        beforeRouteLeave(to, from, next) {
            if (to.path===from.path) {

            } else if ((from.path==='/admin/adminoperation/userOperation'&&from.path==='/admin/adminoperation/blogOperation')||(to.path==='/admin/adminoperation/userOperation'&&from.path==='/admin/adminoperation/blogOperation')) {
                // let temp = this.drafts
                // this.drafts = this.blogs
                // this.blogs = temp
                this.exchange()
                this.changeData()
                next()
            } else {
                next()
            }
        },
        watch:{
            users() {
                this.count = this.users.length
                // console.log('watch----' + this.count);
            }
        },
        methods: {
            exchange() {
                let temp = [...this.blogs]
                this.blogs = [...this.users]
                this.users = [...temp]
            },
            changeData(x=0, y=9){
                // this.count1 = this.blogs.length
                // this.count2 = this.drafts.length
                this.count = this.users.length
                if (this.count>5) {
                    console.log('count>9');
                    this.showingData = this.users.slice(x, y)
                } else {
                    console.log('count<=9');
                    this.showingData.splice(0,this.showingData.length)
                    this.showingData = this.users
                }

            },
            handleCurrentChange(val) {
                // console.log(`当前页: ${val}`);
                // this.getData(val, this.pageSize);
                // this.pageNum = val;
                this.changeData((val-1)*9, val*9)
            },
            handleSizeChange(val) {
                // console.log(`每页 ${val} 条`);
                // this.pageSize = val;
            },
            decode(string) {
                return Base64.decode(string)
            },
            handleEdit(index, row) {
                console.log(index, row);
                this.$router.push({
                    path: '/editBlog',
                    query: {
                        article_id: row.article_id
                    }
                })

            },
            handleDelete(index, row) {
                // console.log(index, row);
                let path = '/api'+this.$route.meta.type+this.$route.meta.routerObject
                console.log(path);
                let params = (this.$route.meta.type+'_id').split('/')[1]
                console.log(row[params]);
                this.$axios.get(path, {
                    params: {
                        id: row[params]
                    }
                }
                )
                    .then((response) => {
                        let res = response.data
                        if (res.status) {
                            this.$message.warning(res.msg)
                        } else {
                            this.users.splice(index,1)
                            this.$message.success(res.msg)
                        }
                    })
            }
        }
    }
</script>

<style scoped>
    .el-table{
        position: absolute;
        border-radius: 10px;
        top: 10%;
        left: 2.5%;
        right: 0;
        /*margin: 10px auto;*/
        /*align: center;*/
    }
    .el-pagination{
        text-align: center;
        /*margin: 15px auto;*/
        position: absolute;
        bottom: -35px;
        right: 0;
        left: 0;
    }

</style>
